:root {
    --light-color: #F0FF45;
    --sub-menu-pos: 45px;
    --title-pos: 135px;
}

/*---------------------------------------
            GLOBAL STYLES
----------------------------------------*/
nav {
    display:flex;
    justify-content: flex-start;
    flex-direction: column;
    width: 100vw;
}

 button {
    font-size: calc(12px + 0.8vw);
    background:#F0FF45;
    position: relative;
}

button:hover {
    cursor: pointer;
    opacity: 1;
}

button:focus {
    outline: none;
}
a {
    font-family: core-deco;
    font-weight: 800;
    font-size: calc(10px + 0.8vw);
    line-height: calc(10px + 0.8vw);
    color: rgb(137, 141, 94);
    text-decoration: none;
    position: relative;
}

/*---------------------------------------
            NAV STYLES
----------------------------------------*/


div.header-container {
    position: relative;
    right: 2vw;
    z-index: 3;
}

div.nav-container {
    position: fixed;
    background: rgb(12,54,78);
    width: 100vw;
    right: 24px;
}

div.nav-top {
    order: 1;
    display: flex;
    justify-content: flex-start;
    padding-bottom: 3%;
    height: 5vw;
    z-index: 5;

}

div.navLnks-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-left: 5vw;
    width: 65vw;
}

div.navLnks-container button {
    color: rgb(137, 141, 94);
    background:none;
    border:none;
    outline: none;
    padding: 5px;
    opacity:0.6;
}

div.navLnks-container button:hover {
    cursor: pointer;
    opacity: 1;
    color: #F0FF45;
    text-shadow: 0px 0px 10px currentColor, -0px -0px 10px currentColor;
}

div.navBtns-container {
    display:flex;
    justify-content: space-between;
    margin-left: 10%;
}

.navBtns-container button {
    margin-top: -3vw;
    padding: 5px 5px 0 5px;
    align-self: center;
    background: #D9C76C;
    border: none;
    border-radius: 5px;
    color: rgb(12,54,78);
}

div.navBtns-container button img {
    height: 2vw;
    width: 2vw;
    min-height: 12px;
    min-width: 12px;
    padding-right: 1vw;
    vertical-align: middle;
}

div.nav-bottom {
    order:2;
    position: absolute;
    top: 35px;
    width: 98vw;
    height: 30%;
}


div.subMenu-container, div.subMenu-container-open {
    position: absolute;
    display: flex;
    justify-content: space-between;
    width: 50vw;
    top: 45px;
    top: var(--sub-menu-pos);
    z-index:  5;
    right: 45%;
    -webkit-transform: translateY(20px) rotate(-4.3deg);
            transform: translateY(20px) rotate(-4.3deg);
    -webkit-transition:  -webkit-transform 400ms ease-out;
    transition:  -webkit-transform 400ms ease-out;
    transition:  transform 400ms ease-out;
    transition:  transform 400ms ease-out, -webkit-transform 400ms ease-out;
}

div.subMenu-container a, div.subMenu-container-open a {
    color: #0c364e;
    margin: 0 5px;
    opacity: 0;
    -webkit-transition: opacity 200ms ease;
    transition: opacity 200ms ease;
}

div.subMenu-container a {
    pointer-events: none;
}

div.subMenu-container-open{
    -webkit-transform: translateY(5px) rotate(-4.3deg);
            transform: translateY(5px) rotate(-4.3deg);
    -webkit-transition: -webkit-transform 400ms ease-out;
    transition: -webkit-transform 400ms ease-out;
    transition: transform 400ms ease-out;
    transition: transform 400ms ease-out, -webkit-transform 400ms ease-out;
    visibility: visible;

}

div.subMenu-container-open a {
    font-weight: bolder;
    opacity: 1;
    -webkit-transition: opacity 200ms ease, -webkit-transform 400ms ease;
    transition: opacity 200ms ease, -webkit-transform 400ms ease;
    transition: opacity 200ms ease, transform 400ms ease;
    transition: opacity 200ms ease, transform 400ms ease, -webkit-transform 400ms ease;
}

div.subMenu-container-open a:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    -webkit-transition: -webkit-transform 400ms ease;
    transition: -webkit-transform 400ms ease;
    transition: transform 400ms ease;
    transition: transform 400ms ease, -webkit-transform 400ms ease;
    text-shadow: none;

}

div.title-container{
    position: relative;
    width:100%;
}

div.light-container {
    position:relative;
    width:100%;
}

#title-light-figure {
    position: absolute;
    top: calc(-20px - 0.1vw);
    right: 5px;
    z-index: 1;
    width: 3%;
}

#title-light-ray {
    position: absolute;
    top: -25px;
    right: 5px;
}

div.title-container h1 {
    z-index: 3;
    position:fixed;
    color: rgb(12,54,78);
    font-size: 3.5vw;
    line-height: 3.5vw;
    left: 5px;
    top: 135px;
    top: var(--title-pos) ;
}

@media only screen and (min-width: 468px) and (max-width: 768px) {

    div.navBtns-container {
        margin-left: 5%;
    }

}

@media only screen and (max-width: 468px) {

    a, button {
        font-size: 18px;
    }

    div.nav-container {
        width:102vw;
        right:0px;
    }

    div.nav-top {
        position: relative;
        margin-right: 15px;
        padding-top: 5px;
        height: 5.5vw;
    }

    div.navLnks-container {
        margin-left: 2vw;
    }

    div.navLnks-container a {
        margin-top: 8px;
    }

    div.navBtns-container {
        position: absolute;
        right: 2%;
        margin-top:-5px;
    }

    div.navBtns-container button {
        margin-top: -1vw;
        padding-top: 5%;
    }

    div.subMenu-container, div.subMenu-container-open {
        flex-direction: column;
        width: auto;
        top: 5vh;
        right: -110px;
        -webkit-transform: rotate(0);
                transform: rotate(0);
        -webkit-transition: right 800ms ease-in;
        transition: right 800ms ease-in;
    }
    
    div.subMenu-container a, div.subMenu-container-open a {
        color: #0c364e;
        background: #D9C76C;
        text-align: left;
        margin: 1vh 0;
        padding: 10px 10px 10px 5px;
        border-radius: 2px;
        opacity: 1;
        -webkit-transition: opacity 200ms ease;
        transition: opacity 200ms ease;
    }
    
    div.subMenu-container-open{
        right: -10px;
        visibility: visible;
        -webkit-transition: right 200ms ease-out;
        transition: right 200ms ease-out;
    }

    div.subMenu-container a {
        right: -120px;
    }
    div.subMenu-container a:nth-child(1), div.subMenu-container-open a:nth-child(1) {
        -webkit-transition:  right 200ms ease, opacity 200ms ease, -webkit-transform 200ms ease;
        transition:  right 200ms ease, opacity 200ms ease, -webkit-transform 200ms ease;
        transition:  right 200ms ease, opacity 200ms ease, transform 200ms ease;
        transition:  right 200ms ease, opacity 200ms ease, transform 200ms ease, -webkit-transform 200ms ease;
    }
    div.subMenu-container a:nth-child(2), div.subMenu-container-open a:nth-child(2) {
        -webkit-transition:  right 400ms ease-out, opacity 200ms ease, -webkit-transform 200ms ease;
        transition:  right 400ms ease-out, opacity 200ms ease, -webkit-transform 200ms ease;
        transition:  right 400ms ease-out, opacity 200ms ease, transform 200ms ease;
        transition:  right 400ms ease-out, opacity 200ms ease, transform 200ms ease, -webkit-transform 200ms ease;
    }
    div.subMenu-container a:nth-child(3), div.subMenu-container-open a:nth-child(3) {
        -webkit-transition:  right 600ms ease, opacity 200ms ease, -webkit-transform 200ms ease;
        transition:  right 600ms ease, opacity 200ms ease, -webkit-transform 200ms ease;
        transition:  right 600ms ease, opacity 200ms ease, transform 200ms ease;
        transition:  right 600ms ease, opacity 200ms ease, transform 200ms ease, -webkit-transform 200ms ease;
    }
    div.subMenu-container a:nth-child(4), div.subMenu-container-open a:nth-child(4) {
        -webkit-transition:  right 800ms ease, opacity 200ms ease, -webkit-transform 200ms ease;
        transition:  right 800ms ease, opacity 200ms ease, -webkit-transform 200ms ease;
        transition:  right 800ms ease, opacity 200ms ease, transform 200ms ease;
        transition:  right 800ms ease, opacity 200ms ease, transform 200ms ease, -webkit-transform 200ms ease;
    }
    div.subMenu-container a:nth-child(5), div.subMenu-container-open a:nth-child(5) {
        -webkit-transition:  right 1000ms ease, opacity 200ms ease, -webkit-transform 200ms ease;
        transition:  right 1000ms ease, opacity 200ms ease, -webkit-transform 200ms ease;
        transition:  right 1000ms ease, opacity 200ms ease, transform 200ms ease;
        transition:  right 1000ms ease, opacity 200ms ease, transform 200ms ease, -webkit-transform 200ms ease;
    }
    /* TODO(GET NEW BOLDER FONT FOR SUBMENU LNKS) */
    div.subMenu-container-open a {
        right: -10px;
        font-weight: bolder;
        opacity: 1;
        z-index: 1;
        -webkit-transform: scale(1) translateX(0);
                transform: scale(1) translateX(0);
    }
    
    div.subMenu-container-open a:hover {
        -webkit-transform: scale(1.1) translateX(-5px);
                transform: scale(1.1) translateX(-5px);
        -webkit-transition: -webkit-transform 200ms ease;
        transition: -webkit-transform 200ms ease;
        transition: transform 200ms ease;
        transition: transform 200ms ease, -webkit-transform 200ms ease;
        text-shadow: none;
    }

    div.title-container {
        right:7px;
    }

    div.title-container h1 {
        font-size: 4.5vw;
    }

    #title-light-ray {
        right: -4%;
    }

    #title-light-figure {
        right: -4%;
        top: calc(-20px - 0.1vw);
    }
    
    
    
}


