div.characters-container {
    margin: 0 10px;
}

.characters-container button {
    border: none;
    background: none;
    color: white;
    padding: 5px;
}

.characters-container h2 {
    text-decoration: underline;
}

button.guide-text::before {
    content: "";
    height: 0px;   
    width: 0px;   
    position: absolute;    
    right: 10px;    
    border-left: 15px solid transparent;    
    border-right: 7px solid black;    
    border-bottom: 5px solid transparent;    
    border-top: 10px solid black;    
    bottom: -12px;
}

button.guide-text {
    position: absolute;
    color: white;
    background: black;
    border: none;
    outline: none;
    cursor: default;
    height: auto;
    width: 23vw;
    right: 650px;
    top: -5px;
    text-align: center;
    border-radius: 5px;
    padding: 5px;
    font-size: calc(10px + 0.8vw);
    z-index:-1;
    opacity: 0;
    -webkit-transition: opacity 400ms ease;
    transition: opacity 400ms ease;
}

button.guide-text:focus {
    top: -5px;
    opacity: 1;
    z-index: 1;
}

div.character-info[data-status=Victim]>button{
    cursor: default;
}

